import { useFile } from "@rp/react/tanstack-api/file"
import { useWatchlist } from "@rp/react/tanstack-api/watchlist"

type UseChatTitleParams = {
  files: {
    fileIds: Array<string>
    multipleTitle: string
  }

  watchlist: {
    watchlistId: string | undefined
  }
}

const useChatTitle = ({ files, watchlist }: UseChatTitleParams) => {
  const { data: file, isLoading: isFileTitleLoading } = useFile(files.fileIds[0])
  const { data: watchlistData, isLoading: isWatchlistLoading } = useWatchlist(watchlist.watchlistId)

  if (files.fileIds.length === 0 && watchlist.watchlistId === undefined)
    return { title: undefined, isLoading: false }
  if (files.fileIds.length === 1) return { title: file?.name ?? "", isLoading: isFileTitleLoading }
  if (files.fileIds.length > 1) return { title: files.multipleTitle, isLoading: false }
  if (watchlist.watchlistId !== undefined && watchlistData?.name)
    return { title: `Chat with ${watchlistData?.name}`, isLoading: isWatchlistLoading }

  return { title: undefined, isLoading: false }
}

export { useChatTitle }
